.homeContainer {
    font-family: 'Work Sans', sans-serif;
}


.heading {
    text-align: left;
    width: 100%;
}

.notes {
    display: flex;
    flex-direction: column;
    grid-template-columns: auto auto auto;
    grid-row-gap: 20px;
}

.notesContainerInner {
    background: white;
    overflow: auto;
}

.noteContainer {
    width: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.note {
    width: 17rem;
    border-radius: 6px;
    padding: 0.5rem;
}

.notranslate {
    background-color: transparent;
    border: none;
    padding: 0;
    outline: none;
    overflow: hidden;
    resize: none;
    vertical-align: top;
    width: 100%;
}

.textBox {
    font: inherit;
    background-color: transparent;
    border: none;
    width: 100%;
    min-height: 50px;
    max-height: 150px;
    font-size: 13px;
}

.textBox:focus {
    outline: none;
}

.noteFooter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.starIcon {
    cursor: pointer;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.deleteIcon {
    color: black;
    cursor: pointer;
}

.footerDropDown {
    position: absolute;
    background-color: white;
    width: 5rem;
    padding: 10px 0;
    top: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 5px;
    border-radius: 10px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.pinIcon {
    color: rgb(108, 108, 249);
    cursor: pointer;
}

.delete {
    color: rgb(255, 73, 73);
    cursor: pointer;
}

