.sideNav{
    /* max-width: 7rem; */
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    
}
.sideNavInner{
    width: 100%;
    height: 100%;
    /* border-right: 1px solid rgb(223, 223, 223); */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.addBtn{
    width: 35px;
    height: 35px;
    background-color: black;
    border: none;
    border-radius: 50%;
    color: white;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}
.colors{
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    margin-top: 2rem;
}
.color{
    background-color: aqua;
    width: 1.3rem;
    height: 1.3rem;
    border-radius: 50%;
    cursor: pointer;
}
#c1{
    background-color: #FEC971;
}
#c2{
    background-color: #FE9B71;
}
#c3{
    background-color: #B592FD;
}
#c4{
    background-color: #00D4FE;
}
#c5{
    background-color: #E3EE8E;
}

.loVtXk {
    display: flex;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    position: sticky;
    bottom: 0px;
    padding: 16px 0px;
    background-color: rgb(255, 255, 255);
    transition: box-shadow 0.2s ease 0s;
    box-shadow: rgba(0, 0, 0, 0.125) 0px 0px 30px;
}

.dYiFiD {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    border: 0px;
    background: none;
    cursor: pointer;
}